import { TOAST_STYLES } from "assets/base-theme";
import BaseToast, { IBaseToastProps } from "components/base/BaseToast";
import React from "react";
import { INPUT_ERROR_TYPE } from "utils/Constant";
import { generateClasses } from "utils/Util";
import check from "assets/img/toasts/check.svg";
import close from "assets/img/toasts/close.svg";
import checkInfo from "assets/img/toasts/infoCheck.svg";
import checkWarning from "assets/img/toasts/warningCheck.svg";
import checkFail from "assets/img/toasts/errorIcon.svg";
import Text from "components/Text/Text";

const ToastCustom: React.FC<any> = React.forwardRef((props: any, ref: any) => {
  const getClassNames = () => {
    const styleObject = {
      ...TOAST_STYLES,
      overrideStyle: props.styleObj,
      overrideClasses: props.className,
      isDisabled: props.disabled,
      applyErrorStyle: props.validation && props.errorType == INPUT_ERROR_TYPE.BORDER,
      removeStyleProperty: [],
      removeClasses: [],
    };

    return generateClasses(styleObject, props.variant);
  };
  const onClose = (ref: any) => {
    ref.clear();
  };
  React.useImperativeHandle(ref, () => ({
    showFunction() {
      let checkIcon, closeIcon, bgColor;
      switch (props.variant) {
        case "PRIMARY":
          checkIcon = check;
          closeIcon = close;
          break;
        case "SECONDARY":
          checkIcon = checkInfo;
          closeIcon = close;
          break;
        case "TERTIARY":
          checkIcon = checkWarning;
          closeIcon = close;
          break;
        case "QUATERNARY":
          checkIcon = checkFail;
          closeIcon = close;
          break;
        default:
          checkIcon = check;
          closeIcon = close;
      }
      ref.show({
        life: 3000,
        summary: (
          <div className="flex justify-between items-center my-auto ">
            <div className="my-auto w-6 h-6 flex justify-center items-center">
              <img src={checkIcon} alt="" />
            </div>
            {props.customContent ? (
              props.toastText()
            ) : (
              <div className="my-auto ">
                <Text
                  className="text-xs font-Roboto text-primary-ptext-1000 mx-4.5 truncate"
                  label={props.toastText}
                />
              </div>
            )}
            <div
              className="w-6 h-6 flex justify-center items-center cursor-pointer"
              onClick={() => {
                onClose(ref);
                if (props.onRemove) {
                  props.onRemove();
                }
              }}
            >
              <img src={closeIcon} alt="" />
            </div>
          </div>
        ),
        contentClassName: getClassNames() + "min-w-75 ",
        //sticky: true,
      });
    },
  }));

  const { position, onRemove, onShow } = props;

  return (
    <>
      <BaseToast position={position} onRemove={onRemove} ref={(el) => (ref = el)} onShow={onShow} />
    </>
  );
});

// Memoization

export default React.memo(ToastCustom);
// redux wiring
